import React, { Component } from 'react';
import { Link } from 'react-router-dom'; 
import Background  from '../src/resources/backgroung.jpg';
import urlConst from '../src/config/config' 

export default class App extends Component{     
    state={
        email:'',
        password:'',
        users : [],
        validation:{
            email:null,
            password:null,
        }
    };
    history: any;
    constructor(props : any){
        super(props);
        this.formInputChange= this.formInputChange.bind(this);
        this.formSubmit= this.formSubmit.bind(this);
      }   
    componentWillMount(){
        localStorage.token=null;
        localStorage.setItem('token', '');  
    }
     formInputChange(event : any){
        this.setState({
            [event.target.name] : event.target.value
        });
    
        const validation: any = this.state.validation;
        if(!event.target.value){
            validation[event.target.name]='Can\'t be empty';
        }
        else{
            validation[event.target.name]=null;
        }
    
        this.setState({
            validation
        });
     }    

     formSubmit(event : any){
        event.preventDefault();
        const users : any[]= this.state.users;
        users.push({
            email: this.state.email,
            password: this.state.password
        })
        this.setState({
            users,
            email: '',
            password: ''
        })
        console.log(users);
        const axios = require('axios'); 
        axios.post(urlConst.url + '/loginController/login', {
            email:users[0].email,
            password: users[0].password
          })
          .then((response: any) =>{
            if(response.data.token!==null && response.data.token!==''){           
                const token = `Bearer,${response.data.token}`;//response.data.token;
                localStorage.setItem('token', `Bearer,${response.data.token}`);//setting token to local storage                
                axios.defaults.headers.common['authorization'] = token;//setting authorize token to header in axios   
                window.location.href = '/dashboard';
            }
            else{
                response.data.token=null;
                console.log(response.data);
                window.location.href = '/';
            }
          });
     }
    render(){
        return(
            <div style={{ backgroundColor: "#191717", backgroundImage: `url(${Background})`,  height: "657px", backgroundSize: 'cover' }}>
            <div className='container' >
            <br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br />                
           <div className='container col-5 mt-5'>
                  <form onSubmit={this.formSubmit}>     
                      <div className='form-row mb-3'>
                          <input type='text' name='email' placeholder='Email' className='form-control' value={this.state.email} 
                          onChange={this.formInputChange}/>
                          {this.state.validation.email ? <span style={{color:'red'}}>{this.state.validation.email}</span> : null}
                      </div>
                      <div className='form-row mb-3'>
                          <input type='text' name='password' placeholder='Password' className='form-control' value={this.state.password}
                          onChange={this.formInputChange}/>
                          {this.state.validation.password ? <span style={{color:'red'}}>{this.state.validation.password}</span> : null}
                      </div>
                      <div className='form-row mb-3'>
                          <button type='submit' className='btn btn-success'>Sign In</button>
                          <Link style={{ color:"#00ff95"}} to="/registration" className='ml-5'>Not registered </Link>  
                      </div>
                  </form>                
              </div>
          </div> 
        </div>   
        );
    }
    }



   